import { useMutation } from '@apollo/react-hooks';
import { useInput } from '../hooks/useInput';
import * as S from './styles/Form';
import { CREATE_ITEM } from '../graphql/mutations';

const createItem = () => {
  const [title, setTitle] = useInput('My title');
  const [price, setPrice] = useInput(123);
  const [description, setDescription] = useInput('My description');
  const [createItem, { data }] = useMutation(CREATE_ITEM);

  const handleSubmit = async event => {
    event.preventDefault();
    await createItem({ variables: { title, price, description } });

    console.log(data);
  };

  return (
    <S.Form onSubmit={handleSubmit}>
      <fieldset>
        <label htmlFor="title">
          Title
          <input type="text" name="title" placeholder="Title" required value={title} onChange={setTitle} />
        </label>
        <label htmlFor="price">
          Price
          <input type="number" name="price" placeholder="Price" required value={price} onChange={setPrice} />
        </label>
        <label htmlFor="description">
          Description
          <textarea
            name="description"
            placeholder="Description"
            required
            value={description}
            onChange={setDescription}
          />
        </label>
        <button type="submit">Submit</button>
      </fieldset>
    </S.Form>
  );
};

export default createItem;
